import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import { AuthProvider, PrivateRoute } from './components/SignUp/useAuth';
import Header from './components/Header/Header';
import Foods from './components/Foods/Foods';
import GroceryDetails from './components/GroceryDetails/GroceryDetails';
import Blog from './components/Blog/Blog';
import Footer from './components/Footer/Footer';
import NotFound from './components/NotFound/NotFound';
import SignUp from './components/SignUp/SignUp';
import Shipment from './components/Shipment/Shipment';
import OrderComplete from './components/OrderComplete/OrderComplete';
import Cart from './components/Cart/Cart';
import Profile from './components/Profile/Profile';

function App() {
  const [cart, setCart] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');

  const cartHandler = (updatedCart) => {
    setCart(updatedCart);
  };

  const [deliveryDetails, setDeliveryDetails] = useState({
    toDoor: "Delivery To Door",
    estimatedTime: "Delivery will be completed within 30-40 minutes"
  });

  const deliveryDetailsHandler = data => {
    setDeliveryDetails(data);
  };

  const checkOutItemHandler = (productId, quantity) => {
    const updatedCart = cart.map(item => {
      if (item.id === productId) {
        return { ...item, quantity };
      }
      return item;
    }).filter(item => item.quantity > 0);
    setCart(updatedCart);
  };

  const clearCart = () => {
    setCart([]);
  };

  return (
    <AuthProvider>
      <Router>
        <Header cart={cart} setSearchQuery={setSearchQuery} />
        <div className="main-content">
          <Switch>
            <Route exact path="/">
              <Foods cart={cart} cartHandler={cartHandler} searchQuery={searchQuery} />
              <Footer />
            </Route>

            <Route path="/grocery/:id">
              <GroceryDetails cart={cart} cartHandler={cartHandler} />
              <Footer />
            </Route>

            <Route path="/search=:searchQuery">
              <Footer />
            </Route>

            <PrivateRoute path="/checkout" component={() => (
              <>
                <Shipment
                  cart={cart}
                  deliveryDetails={deliveryDetails}
                  deliveryDetailsHandler={deliveryDetailsHandler}
                  checkOutItemHandler={checkOutItemHandler}
                  clearCart={clearCart}
                />
                <Footer />
              </>
            )} />

            <PrivateRoute path="/order-complete" component={() => (
              <>
                <OrderComplete deliveryDetails={deliveryDetails} />
                <Footer />
              </>
            )} />

            <PrivateRoute path="/cart" component={() => (
              <>
                <Cart cart={cart} cartHandler={cartHandler} />
                <Footer />
              </>
            )} />

            <PrivateRoute path="/profile" component={() => (
              <>
                <Profile />
                <Footer />
              </>
            )} />

            <Route path="/signup" render={(props) => (
              <SignUp {...props} defaultReturningUser={props.location.state?.defaultReturningUser || false} />
            )} />

            <Route path="*">
              <NotFound />
            </Route>
          </Switch>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
