import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import './Foods.css';

// Import grocery data from different folders
import vegetables from '../../data/vegetables.json';
import milkProducts from '../../data/milkProducts.json';
import readyToEat from '../../data/readyToEat.json';
import limitedOffers from '../../data/limitedOffers.json';

const allGroceries = [...vegetables, ...milkProducts, ...readyToEat, ...limitedOffers];

const Foods = ({ cart, cartHandler, searchQuery }) => {
  const [groceries, setGroceries] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('vegetables');

  useEffect(() => {
    setGroceries(allGroceries);
  }, []);

  const selectedGroceries = groceries.filter((item) => {
    return item.category === selectedCategory && (!searchQuery || item.name.toLowerCase().includes(searchQuery.toLowerCase()));
  });

  const handleAddToCart = (product) => {
    const updatedCart = [...cart];
    const existingProductIndex = updatedCart.findIndex(item => item.id === product.id);

    if (existingProductIndex >= 0) {
      updatedCart[existingProductIndex].quantity += 1;
    } else {
      updatedCart.push({ ...product, quantity: 1 });
    }

    cartHandler(updatedCart);
  };

  const handleRemoveFromCart = (item) => {
    const existingCartItem = cart.find(cartItem => cartItem.id === item.id);
    if (existingCartItem && existingCartItem.quantity > 1) {
      const updatedCart = cart.map(cartItem => {
        if (cartItem.id === item.id) {
          return { ...cartItem, quantity: cartItem.quantity - 1 };
        }
        return cartItem;
      });
      cartHandler(updatedCart);
    } else {
      cartHandler(cart.filter(cartItem => cartItem.id !== item.id));
    }
  };

  return (
    <section className="grocery-area my-5">
      <div className="container">
        <nav>
          <ul className="nav justify-content-center">
            <li className="nav-item" onClick={() => setSelectedCategory('vegetables')}>
              <span className={selectedCategory === 'vegetables' ? 'active nav-link' : 'nav-link'}>
                Vegetables
              </span>
            </li>
            <li className="nav-item" onClick={() => setSelectedCategory('milkProducts')}>
              <span className={selectedCategory === 'milkProducts' ? 'active nav-link' : 'nav-link'}>
                Milk Products
              </span>
            </li>
            <li className="nav-item" onClick={() => setSelectedCategory('readyToEat')}>
              <span className={selectedCategory === 'readyToEat' ? 'active nav-link' : 'nav-link'}>
                Ready to Eat
              </span>
            </li>
            <li className="nav-item" onClick={() => setSelectedCategory('limitedOffers')}>
              <span className={selectedCategory === 'limitedOffers' ? 'active nav-link' : 'nav-link'}>
                Limited Offers
              </span>
            </li>
          </ul>
        </nav>

        <div className="row my-5">
          {selectedGroceries.map((item) => {
            const cartItem = cart.find(cartItem => cartItem.id === item.id);
            return (
              <div key={item.id} className="col-md-4 grocery-item">
                <img src={item.image || 'url_to_default_image'} alt={item.name} onError={(e) => e.target.src = 'url_to_default_image'} />
                <h3>{item.name}</h3>
                <p>Price: {item.price}/{item.unit}</p>
                {cartItem ? (
                  <div className="quantity-controls">
                    <button onClick={() => handleRemoveFromCart(item)}>-</button>
                    <span>{cartItem.quantity}</span>
                    <button onClick={() => handleAddToCart(item)}>+</button>
                  </div>
                ) : (
                  <button onClick={() => handleAddToCart(item)}>Add to Cart</button>
                )}
              </div>
            );
          })}
        </div>

        <div className="text-center">
          {cart.length ? (
            <Link to="/checkout">
              <button className="btn btn-danger">Check Out Your Groceries</button>
            </Link>
          ) : (
            <button disabled className="btn btn-secondary">
              Check Out Your Groceries
            </button>
          )}
        </div>
      </div>
    </section>
  );
};

export default Foods;
