import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import './Header.css';
import Logo from '../../images/background.png';
import CartIcon from '../Cart/cart2.jpg';
import { useAuth } from '../SignUp/useAuth';

// Import grocery data from different folders
import vegetables from '../../data/vegetables.json';
import milkProducts from '../../data/milkProducts.json';
import readyToEat from '../../data/readyToEat.json';
import limitedOffers from '../../data/limitedOffers.json';

const allGroceries = [...vegetables, ...milkProducts, ...readyToEat, ...limitedOffers];

const Header = ({ cart, setSearchQuery }) => {
  const auth = useAuth();
  const [query, setQuery] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const [highlight, setHighlight] = useState(false);
  const history = useHistory();

  useEffect(() => {
    if (query) {
      const filteredSuggestions = allGroceries.filter(item =>
        item.name.toLowerCase().includes(query.toLowerCase())
      );
      setSuggestions(filteredSuggestions);
    } else {
      setSuggestions([]);
    }
  }, [query]);

  useEffect(() => {
    if (cart.length > 0) {
      setHighlight(true);
      const timer = setTimeout(() => setHighlight(false), 500);
      return () => clearTimeout(timer);
    }
  }, [cart]);

  const handleSearch = (e) => {
    e.preventDefault();
    setSearchQuery(query);
    history.push('/');
  };

  const handleSuggestionClick = (item) => {
    setSearchQuery(item.name);
    setQuery('');
    setSuggestions([]);
    history.push('/');
  };


  const finalLogo = 'https://i.postimg.cc/9FfQpdc8/background.webp';
  const finalCartIcon = 'https://i.postimg.cc/zvw5Dfwc/grocery-cart-2.png';
  return (
    <nav className='navbar navbar-expand navbar-light bg-white py-2 sticky-top'>
      <div className="container">
        <Link to='/' className='navbar-brand'>
          <img src={finalLogo} alt="Nilgiri Cart logo" />
        </Link>

        <form className="form-inline my-2 my-lg-0 mx-auto" onSubmit={handleSearch}>
          <input
            className="form-control mr-sm-2"
            type="search"
            placeholder="Search"
            aria-label="Search"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
          />
          <button className="btn btn-outline-success my-2 my-sm-0 ml-2" type="submit">Search</button>
          {suggestions.length > 0 && (
            <ul className="suggestions-list">
              {suggestions.map((item) => (
                <li key={item.id} onClick={() => handleSuggestionClick(item)}>
                  {item.name}
                </li>
              ))}
            </ul>
          )}
        </form>

        <ul className='navbar-nav align-items-center'>
          <li className='nav-item active'>
            <Link to='/checkout' className='nav-link'>
              <img src={finalCartIcon} alt="Cart" className={`cart-icon ${highlight ? 'highlight' : ''}`} />
              <span className='badge bg-light'>{cart.length}</span>
            </Link>
          </li>

          <li className='nav-item'>
            {
              auth.user ?
                <Link to='/profile' className='nav-link'>
                  <img src="/path/to/profile-icon.png" alt="Profile" className="profile-icon" />
                </Link>
                :
                <Link to={{ pathname: '/signup', state: { defaultReturningUser: true } }} className='nav-link'>Login</Link>
            }
          </li>

          <li className='nav-item'>
            {
              auth.user ?
                <Link to='/' className='nav-link'>
                  <button onClick={() => { auth.signOut() }}
                    className='btn btn-danger btn-rounded'
                  >
                    Sign Out
                  </button>
                </Link>
                :
                <Link to={{ pathname: '/signup', state: { defaultReturningUser: false } }} className='nav-link'>
                  <button
                    className='btn btn-danger btn-rounded'
                  >
                    Sign Up
                  </button>
                </Link>
            }
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Header;